import { useEffect, useState } from 'react'

/* eslint-disable camelcase */
export interface DeBankTvlResponse {
  contractaddress : number
  address : string  
  result : number  
}

export const useGetStats = () => {
  const [data, setData] = useState<DeBankTvlResponse | null>(null)

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await fetch('https://api.bscscan.com/api?module=account&action=tokenbalance&contractaddress=0xaDAaE082237cB1772c9e079dB95c117E6Dd0C5aF&address=0x8d3cF6F026f1c8021BD3B0F74263D4D51208917F&tag=latest&apikey=7K36W5M328YT6KSTY4EGMJBHYGH51R7ZUQ')
        
        const responseData: DeBankTvlResponse = await response.json()

        setData(responseData)
      } catch (error) {
        console.error('Unable to fetch data:', error)
      }
    }

    fetchData()
  }, [setData])

  return data 
}
