import React from 'react'
import { Card, CardBody, Text } from '@pancakeswap/uikit'
import BigNumber from 'bignumber.js/bignumber'
import styled from 'styled-components'
import useFarmsWithBalance from 'hooks/useFarmsWithBalance'
import { getCakeAddress, getHVIAddress } from 'utils/addressHelpers'
import useI18n from 'hooks/useI18n'
import { useTranslation } from 'contexts/Localization'
import { useTotalSupply, useBurnedBalance } from 'hooks/useTokenBalance'
import { getBalanceNumber } from 'utils/formatBalance'
import { useLpTokenPrice, usePriceCakeBusd, usePriceHVIBusd} from 'state/hooks'
import CardValue from './CardValue'
import CardValueHuf from './CardValueHuf'


const StyledCakeStats = styled(Card)`
  margin-left: auto;
  margin-right: auto;
  opacity: 0.92;
  max-height: 100%;
`
const CardImage = styled.img`
margin-bottom: 10px;

`
const Row = styled.div`
  align-items: center;
  display: flex;
  font-size: 14px;
  justify-content: space-between;
  margin-bottom: 8px;
`
 const Heading = styled.div`
 font-size: 38px;
 margin-bottom: 20px;
 color: ${({ theme }) => theme.colors.textSubtle};
 font-weight: 900;
  `
const CakeStats = () => {
  const { t } = useTranslation()
  const totalSupply = useTotalSupply()
  const burnedBalance = useBurnedBalance(getHVIAddress()).times(1000000000)
   const eggPrice = usePriceCakeBusd();
  const circSupply = totalSupply ? totalSupply.minus(burnedBalance) : new BigNumber(0);
  const cakeSupply = getBalanceNumber(circSupply);
  const fullSupply = totalSupply ? totalSupply.minus(0) : new BigNumber(0);
  const fullcap = eggPrice.times(fullSupply);
  const vizprice = eggPrice.times(fullcap);
  const egggPrice = usePriceHVIBusd();
  const HVIprice = egggPrice.times(1000000000000000000)
  const HVImarketcap = HVIprice.times(10000000000000000000);


  
  return (
    <StyledCakeStats>
      <CardBody>
        <Heading>
                  {t('HVI Stats')}

        </Heading>
        <CardImage src="/images/tokens/HVI.png" alt="VIZSLASWAP Logo" width={64} height={64} />
        <Row>
          <Text fontSize="17px" color='secondary'>{t('HVI price')}</Text>
          <CardValue fontSize="17px" value={getBalanceNumber(HVIprice)} decimals={17} prefix="$" />
        </Row>
        <Row>
          <Text fontSize="17px" color='secondary'>{t('Market Cap')}</Text>
          <CardValue fontSize="17px" value={getBalanceNumber(HVImarketcap)} decimals={0} prefix="$" />
        </Row>
        <Row>
          <Text fontSize="17px" color='secondary'>{t('Total Supply')}</Text>
          {totalSupply && <CardValue fontSize="17px" value={10000000000000000000} decimals={0} />}
        </Row>
        <Row>
          <Text fontSize="17px" color='secondary'>{t('Total Burned')}</Text>
          <CardValue fontSize="17px" value={getBalanceNumber(burnedBalance)} decimals={0} />
        </Row>

      </CardBody>
    </StyledCakeStats>
  )
}

export default CakeStats
