import { useEffect, useState } from 'react'

/* eslint-disable camelcase */
export interface DeBankTvlResponse {
  contractaddress : number
  address : string  
  result : number  
}

export const useGetStats10 = () => {
  const [data, setData] = useState<DeBankTvlResponse | null>(null)

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await fetch('https://api.bscscan.com/api?module=account&action=tokenbalance&contractaddress=0xDE619A9E0eEeAA9F8CD39522Ed788234837F3B26&address=0x8F71caaB0e4A0eB8615aBfA6A12996cC3d37e988&tag=latest&apikey=NBYG3MAW876XDENZ9EF2NBH482UTTEV5PG')
        
        const responseData: DeBankTvlResponse = await response.json()

        setData(responseData)
      } catch (error) {
        console.error('Unable to fetch data:', error)
      }
    }

    fetchData()
  }, [setData])

  return data 
}
