import React from 'react'
import styled from 'styled-components'
import BigNumber from 'bignumber.js'
import { Card, CardBody, Heading, Skeleton, Text } from '@pancakeswap/uikit'
import { useLpTokenPrice, usePriceBnbBusd, usePriceCakeBusd, usePriceHVIBusd, usePriceHVIBusd2} from 'state/hooks'
import { useTranslation } from 'contexts/Localization'
import { useGetStats } from 'hooks/Vizslaswap-bnb'
import { useGetStats1 } from 'hooks/Vizslaswap-bnbA'
import { useGetStats2 } from 'hooks/Vizslaswap-BNB-B'
import { useGetStats4 } from 'hooks/Vizslaswap-busd'
import { useGetStats5 } from 'hooks/Vizslaswap-busdA'
import { useGetStats6 } from 'hooks/Vizslaswap-busdB'
import { useGetStats7 } from 'hooks/HVI-bnb'
import { useGetStats8 } from 'hooks/HVI-BNBA'
import { useGetStats9 } from 'hooks/HVI-BNBB'
import { useGetStats10 } from 'hooks/HVI-busd'
import { useGetStats11 } from 'hooks/HVI-busdA'
import { useGetStats12 } from 'hooks/HVI-busdB'

const StyledTotalValueLockedCard = styled(Card)`
  align-items: center;
  flex: 1; 
  opacity: 0.85;
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  height: 170px;
      `
  
const TotalValueLockedCard = () =>  {
  const { t } = useTranslation()
  const data = useGetStats()
  const data2 = useGetStats1()
  const data1 = useGetStats2()
  const data4 = useGetStats4()
  const data5 = useGetStats5()
  const data6 = useGetStats6()
  const data7 = useGetStats7()
  const data8 = useGetStats8()
  const data9 = useGetStats9()
  const data10 = useGetStats10()
  const data11 = useGetStats11()
  const data12 = useGetStats12()
  const tvls1 = data1 ? data1.result.toLocaleString('en-US', { maximumFractionDigits: 0 })   : null 
  const tvls = data2 ? data2.result.toLocaleString('en-US', { maximumFractionDigits: 0 })   : null
  const eggPrice = usePriceCakeBusd();
  const HVIPrice = usePriceHVIBusd();
  const HVIPrice1= usePriceHVIBusd2();
  const tvl = data ? data.result.toLocaleString('en-US', { maximumFractionDigits: 0 })   : null
  const tvl4 = data4 ? data4.result.toLocaleString('en-US', { maximumFractionDigits: 0 })   : null
  const tvl5 = data5 ? data5.result.toLocaleString('en-US', { maximumFractionDigits: 0 })   : null
  const tvl6 = data6 ? data6.result.toLocaleString('en-US', { maximumFractionDigits: 0 })   : null
  const tvl7 = data7 ? data7.result.toLocaleString('en-US', { maximumFractionDigits: 0 })   : null
  const tvl8 = data8 ? data8.result.toLocaleString('en-US', { maximumFractionDigits: 0 })   : null
  const tvl9 = data9 ? data9.result.toLocaleString('en-US', { maximumFractionDigits: 0 })   : null
  const tvl10 = data10 ? data10.result.toLocaleString('en-US', { maximumFractionDigits: 0 })   : null
  const tvl11 = data11 ? data11.result.toLocaleString('en-US', { maximumFractionDigits: 0 })   : null
  const tvl12 = data12 ? data12.result.toLocaleString('en-US', { maximumFractionDigits: 0 })   : null
  const CAP5 = HVIPrice.times(tvl7)
  const CAP5A = CAP5.div(tvl8)
  const CAP5B = CAP5A.times(tvl9)
  const CAP5Half = CAP5B.times(2)
  const CAP5Full = CAP5Half.div(1000000000)
  const CAP4= eggPrice.times(tvl4);
  const CAP4A = CAP4.div(tvl5)
  const CAP4B = CAP4A.times(tvl6)
  const CAP4Half = CAP4B.times(2)
  const CAP4Full = CAP4Half.div(1000000000000000000)
  const CAP6 = HVIPrice1.times(tvl10)
  const CAP6A = CAP6.div(tvl11)
  const CAP6B = CAP6A.times(tvl12)
  const CAP6Half = CAP6B.times(2)
  const CAP6Full = CAP6Half.div(1000000000)
  const fullcap = eggPrice.times(tvl);
  const fullcap2 = fullcap.div(tvls)
  const fullcap3 = fullcap2.times(tvls1)
  const fullcap4 = fullcap3.times(2)
  const fullcap5 = fullcap4.div(1000000000000000000)
  const teljes = fullcap5.plus(CAP4Full).plus(CAP5Full).decimalPlaces(0)

  return (
    <StyledTotalValueLockedCard >
      <CardBody >
        <Heading scale="lg" mb="0px" color="secondary">
          {t('Total Value Locked (TVL)')}
        </Heading>
        {data ? (
          <>
            <Heading  scale="xl"  >{`$${teljes}` } </Heading>
            <Text color="textSubtle">{t('Across all LPs and VIZSLASWAP Pool')}</Text>
          </>
        ) : (
          <Skeleton height={66} />
        )}
      </CardBody>
    </StyledTotalValueLockedCard>
  )
}

export default TotalValueLockedCard
