import { useEffect, useState } from 'react'

/* eslint-disable camelcase */
export interface DeBankTvlResponse {
  contractaddress : number
  result : number  
}

export const useGetStats12 = () => {
  const [data1, setData] = useState<DeBankTvlResponse | null>(null)

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await fetch('https://api.bscscan.com/api?module=account&action=tokenbalance&contractaddress=0x8F71caaB0e4A0eB8615aBfA6A12996cC3d37e988&address=0x3FF00B58e432B0895e3fd69634Ec50e3931110c1&tag=latest&apikey=5XY3FPZB96KUQZHKPE5B1FTWPYV95JURIB')
        
        const responseData: DeBankTvlResponse = await response.json()

        setData(responseData)
      } catch (error) {
        console.error('Unable to fetch data:', error)
      }
    }

    fetchData()
  }, [setData])

  
  return data1
}
