import { useEffect, useState } from 'react'

/* eslint-disable camelcase */
export interface DeBankTvlResponse {
  contractaddress : number
  address : string  
  result : number  
}

export const useGetStats7 = () => {
  const [data, setData] = useState<DeBankTvlResponse | null>(null)

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await fetch('https://api.bscscan.com/api?module=account&action=tokenbalance&contractaddress=0xDE619A9E0eEeAA9F8CD39522Ed788234837F3B26&address=0x0388cc967e382A1c85161453bc6cEaA70E59E33e&tag=latest&apikey=7K36W5M328YT6KSTY4EGMJBHYGH51R7ZUQ')
        
        const responseData: DeBankTvlResponse = await response.json()

        setData(responseData)
      } catch (error) {
        console.error('Unable to fetch data:', error)
      }
    }

    fetchData()
  }, [setData])

  return data 
}
