import { useEffect, useState } from 'react'

/* eslint-disable camelcase */
export interface DeBankTvlResponse {
  contractaddress : number
  result : number  
}

export const useGetStats1 = () => {
  const [data2, setData] = useState<DeBankTvlResponse | null>(null)

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await fetch('https://api.bscscan.com/api?module=stats&action=tokensupply&contractaddress=0x8d3cF6F026f1c8021BD3B0F74263D4D51208917F&apikey=5XY3FPZB96KUQZHKPE5B1FTWPYV95JURIB')
        
        const responseData: DeBankTvlResponse = await response.json()

        setData(responseData)
      } catch (error) {
        console.error('Unable to fetch data:', error)
      }
    }

    fetchData()
  }, [setData])

  return data2
}
