import { useEffect, useState } from 'react'

/* eslint-disable camelcase */
export interface DeBankTvlResponse {
  contractaddress : number
  result : number  
}

export const useGetStats2 = () => {
  const [data1, setData] = useState<DeBankTvlResponse | null>(null)

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await fetch('https://api.bscscan.com/api?module=account&action=tokenbalance&contractaddress=0x8d3cF6F026f1c8021BD3B0F74263D4D51208917F&address=0x3FF00B58e432B0895e3fd69634Ec50e3931110c1&tag=latest&apikey=Z6XDXK78A5RHSDYXFAR34RFWBBDNKRIDFH')
        
        const responseData: DeBankTvlResponse = await response.json()

        setData(responseData)
      } catch (error) {
        console.error('Unable to fetch data:', error)
      }
    }

    fetchData()
  }, [setData])

  
  return data1
}
